<template>
  <div id="app">
    <div class="i-banner animated" id="banner"></div>
    <div class="i-main">
      <div class="i-circle animated" id="circle"></div>
      <div class="i-blue__circle animated" id="blueCircle"></div>
      <div class="i-black__circle360 animated" id="blackCircle"></div>
      <div class="i-white__circle animated" id="whiteCircle"></div>
      <div class="i-lightblue__circle animated" id="lightCircle"></div>
      <div class="i-gray__circle animated" id="grayCircle"></div>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
  import { toolMixins } from './mixins/tools'

  export default {
    name: 'app',
    mixins: [toolMixins],
    methods: {
      animationend () {
        let grayCircle = document.querySelector('#grayCircle')
        this.removeClass(grayCircle, 'animated')
      }
    },
    mounted () {
      let [grayCircle, _this] = [document.querySelector('#grayCircle'), this]
      grayCircle.addEventListener('webkitAnimationEnd', function () {
        _this.animationend()
      })
      grayCircle.addEventListener('animationend', function () {
        _this.animationend()
      })
    }
  }
</script>

<style lang="less">
  @import "assets/css/reset";
  @import "assets/css/base";
  @import "assets/css/style";
</style>



